* {
	box-sizing: border-box;
}

button {
	font: inherit;
}

body {
	min-height: 100%;
	position: relative;
	overflow-x: hidden;
	margin: 0;
}

img {
	max-width: 100%;
	max-height: 100%;
	height: auto;
	vertical-align: bottom;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-touch-callout: none;
}
a, a:hover, a:focus {
	text-decoration: none;
}

li {
	outline: none;
}

input[type=submit],
button {
	cursor: pointer
}

